.MainContainer {
  display: flex;
  justify-content: center;
  height: 90vh;
  margin: 0;
  align-items: center;
  font-family: Helvetica, sans-serif;
}
.Container {
  display: flex;
  justify-content: center;
  // align-items: center;
  flex-direction: column;
  height: 70vh;
  width: 57vw;
  color: white;
  .Header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2rem;
    text-align: center;
    font-size: 17px;

    h1 {
      font-weight: bold;
      font-family: sans-serif;
      text-align: center;
      font-size: 3rem;
      border-bottom: 1px solid gray;
      width: 20rem;
      border-radius: 20px;
      margin-bottom: 2rem;
    }
    span {
      color: purple;
    }
    p {
      font-size: 20px;
    }
  }
  .Info {
    background-color: black;
    border-radius: 20px;
    padding: 10px;
    display: flex;
    height: 38rem;
    justify-content: space-between;

    .Image {
      padding: 1rem;
      width: 28vw;
      img {
        margin-top: 30px;
        width: 25vw;
      }
    }
    .Text {
      padding: 1rem;
      width: 28vw;
    }
    h2 {
      margin-top: 2rem;
    }
    p {
      margin-top: 1rem;
      line-height: 23px;
    }
    .MoreInfo {
      display: flex;
      align-items: center;
      margin-top: 1rem;
      margin-bottom: 2rem;
    }
    .icon {
      border: purple 1px solid;
      background: black;
      color: purple;
      font-size: 3rem;
      padding: 10px;
      border-radius: 50px;
      margin-left: 25px;
      margin-right: 20px;
    }
    span {
      color: white;
      font-size: 0.9rem;
      div {
        color: gray;
      }
    }
  }
  .Button {
    color: white;
    text-decoration: none;
    background-color: #840b8f;
    height: 3rem;
    line-height: 50px;
    padding: 10px;
    font-size: 13px;
    text-transform: uppercase;
    font-weight: 600;
    border: none;
    border-radius: 3px;
    &:hover {
      border: 1px solid #840b8f;
      background-color: #222121;
      color: white;
    }
  }
}
// ipad view
@media (max-width: 768px) {
  .MainContainer {
    height: 120vh;
    align-items: flex-start;
  }

  .Container {
    justify-content: start;
    align-items: center;
    width: 70vw;
    .Header {
      span {
        color: purple;
      }
      p {
        font-size: 20px;
      }
    }
    .Info {
      border-radius: 10px;
      align-items: center;
      width: 90vw;
      flex-direction: column;
      height: 90vh;

      .Image {
        width: 50vw;
        img {
          margin-top: 10px;
          width: 40vw;
          border-radius: 50%;
        }
      }
      .Text {
        padding: 1rem;
        width: 85vw;
      }
      h2 {
        margin-top: 1rem;
      }
      p {
        text-align: justify;
        margin-top: 1rem;
      }

      .MoreInfo {
        margin-top: 10px;
      }
    }
  }
}

// mobile view
@media (max-width: 414px) {
  .MainContainer {
    height: 100vh;
  }
  .Container {
    width: 85vw;
    height: 60vh;
    .Header {
      h1 {
        font-size: 1.9rem;
        width: 12rem;
        border-radius: 1px;
        margin-top: 1rem;
      }
      p {
        font-size: 17px;
        margin: 0;
      }
    }
    .Info {
      display: flex;
      background-color: transparent;
      border-radius: 10px;
      align-items: center;
      height: 70vh;
      flex-direction: column;
      justify-content: space-between;
      padding: 1px;
      .Image {
        margin: 0;
        img {
          margin-top: 1px;
          width: 40vw;
          border-radius: 50%;
        }
      }
      .Text {
        padding: 1rem;
        width: 85vw;
        text-align: justify;
        font-size: 17px;
      }
      h2 {
        margin-top: 10px;
      }
      p {
        text-align: justify;
      }
      .MoreInfo {
        margin-top: 10px;
        margin-bottom: 1rem;
      }
    }
  }
}
